/* src/styles/App.css */
/* Base container for old layout — no longer needed */
.app-container {
  display: none; /* ⛔ Deprecated, handled by .app-layout now */
}

/* ✅ New two-column layout */
.app-layout {
  display: flex;
  min-height: 100vh;
  background-color: #111;
  color: white;
  font-family: 'Segoe UI', sans-serif;
}

/* Sidebar will be fixed-width; handled in Sidebar.css */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  overflow: hidden;
}

/* Section area that changes with tabs */
.section-container {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
  animation: fadeIn 0.4s ease;
}

/* Fade-in transition */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.section-transition {
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.4s ease;
}

.section-transition.active {
  opacity: 1;
  transform: translateY(0);
}

/* Responsive tweaks */
@media (max-width: 768px) {
  .app-layout {
    flex-direction: column;
  }

  .main-content {
    padding: 0.5rem;
  }

  .contact-wrapper {
    flex-direction: column;
    gap: 20px;
  }

  .contact-links {
    flex-direction: column;
  }
}
