/* src/styles/Sidebar.css */

.sidebar {
  background-color: #1e1e1e;
  color: #fff;
  width: 300px;
  min-height: 100vh;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  position: fixed;
  left: 0;
  top: 0;
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
}

.sidebar-toggle {
  display: none;
  position: absolute;
  top: 10px;
  right: -50px;
  background-color: #0ea5e9;
  border: none;
  padding: 10px;
  border-radius: 6px;
  color: white;
  cursor: pointer;
  z-index: 1001;
}

.sidebar-content {
  margin-top: 40px;
}

.profile-pic {
  width: 100%;
  max-width: 160px;
  border-radius: 50%;
  margin: 0 auto 20px;
  display: block;
  border: 3px solid #0ea5e9;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.contact-info {
  font-size: 0.9rem;
  margin-bottom: 20px;
}

.contact-info p {
  margin: 10px 0;
  display: flex;
  align-items: center;
  gap: 8px;
  line-height: 1.4;
}

.contact-info a {
  color: #0ea5e9;
  text-decoration: none;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 15px;
  font-size: 1.5rem;
}

.social-icons a {
  color: #fff;
  transition: color 0.3s;
}

.social-icons a:hover {
  color: #0ea5e9;
}

/* Responsive */
@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
    width: 260px;
    padding: 16px;
  }

  .sidebar.open {
    transform: translateX(0);
  }

  .sidebar-toggle {
    display: block;
  }
}
